import './App.css';
import Navbar from './Componants/Navbar';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';
import About from './Componants/About';
import MoreAboutMe from './Componants/MoreAboutMe';
import MyPastEducation from './Componants/MyPastEducation';
import MyPastEducation2 from './Componants/MyPastEducation-copy';
import MyTopSkills from './Componants/MyTopSkills';
import MyRecentProjects from './Componants/MyRecentProjects';
import MyRecentProjects2 from './Componants/MyRecentProjects-copy';
import ContactMe from './Componants/ContactMe';
import DarkModeButton from './Componants/DarkModeButton';
import { useState } from 'react';
import "aos/dist/aos.css";
import AOS from 'aos';
import { useEffect } from 'react';
import {
  HashRouter as Router,
  Routes,
  Route
} from "react-router-dom";
import Copyright from './Componants/Copyright';

function App() {
  const [aosTog, setAosTog] = useState(false);
  const toggleAosTog = () => {
    setAosTog(!aosTog);
  }
  useEffect(() => {
    AOS.init({
      duration: 300, offset: 300,
      // once: true
    });
  }, []);
  const [darkMode, setDarkMode] = useState(true);
  const dark = darkMode ? "dark" : "light"
  const toggleDarkMode = () => {
    setDarkMode(!darkMode);
  }
  return (

    <Router>
      <div className={dark + " transition main" + (aosTog ? " inactive" : "")}>

        <Navbar darkMode={darkMode} />
        <Routes>
          <Route index element={
            <>
              <div className="pt-3" >
                <About />
                <MoreAboutMe />
                <MyPastEducation />
                <MyTopSkills />
                <MyRecentProjects />
                <ContactMe />
              </div>
            </>
          } />
          <Route path="about" element={
            <>
              <div className='pt-3'>
                <MoreAboutMe />
                <MyPastEducation2 />
              </div>
            </>
          } />

          <Route path="skills" element={
            <>
              <div className='pt-3'>
                <MyTopSkills />
                <MyRecentProjects2 />
              </div>
            </>
          } />

          <Route path="contact-me" element={
            <>
              <div className='pt-3'>
                <ContactMe />
              </div>
            </>
          } />

        </Routes>

        <DarkModeButton darkMode={darkMode} toggleAosTog={toggleAosTog} toggleDarkMode={toggleDarkMode} />
        {/* <Copyright /> */}

      </div>
    </Router>

  );
}

export default App;
